.header>.top_header {
    background: transparent url(../images/header_line.png) repeat-x right top;
}

.header>.top_header>.game_header {
    background: transparent url(../images/header.png) no-repeat center top;
    height: 100px;
}

@media only screen and (max-width: 680px) {
    .header>.top_header>.game_header {
        height: 70px;
        background-position: 55% 55%;
    }
}

.header>.t-navbar {
    background: rgb(239, 239, 239);
    background-image: linear-gradient(rgb(255, 255, 255), rgb(215 215 215));
    max-height: 45px;
    border-top: 1px solid silver;
    box-shadow: 0px 4px 4px 0px #b9b9b9;
    padding: 8px 0px;
}

.header>.t-navbar .nav-link button {
    padding: 3px 5px;
}

.header>.t-navbar .nav-link {
    margin: 0px 5px;
}

@media only screen and (max-width: 680px) {
    .header>.t-navbar .nav-link {
        margin: 0px 4px;
        padding: 0;
    }
}

.header>.t-navbar img {
    user-select: none;
}

/* .header>.t-navbar .msTime {
    float: left;
    font-size: 14px;
    position: absolute;
    right: 0px;
} */

.header>.t-navbar .header-items>.server-time {
    /* position: absolute;
    right: 0px; */
    direction: ltr;
    /* float: left; */
    position: absolute;
    left: 10px;
    top: 5px;
    font-size: 14px;
    font-family: consolas, monospace;
}

.header>.t-navbar .header-items {
    position: relative;
    display: block;
    text-align: center;
    min-width: 640px;
}

.header>.t-navbar .header-items>* {
    display: unset;
}

@media only screen and (max-width: 680px) {
    .header>.t-navbar .header-items {
        min-width: 100%;
        text-align: right;
    }
    .header>.t-navbar>div>.mx-auto {
        margin: 0px !important;
        width: 100%;
    }
}