.side_navi {
    margin-left: 20px;
    font-size: 13px;
    color: #000000;
}

.side_navi p {
    width: 150px;
    float: left;
    margin-top: 15px;
}

.side_navi a {
    display: block;
    color: #000000;
    text-decoration: none;
    padding-right: 15px;
    font-weight: normal;
}

.side_navi a:hover {
    color: #00BC00;
    border-top: 1px solid silver;
    border-bottom: 1px solid silver;
    margin: -1px 0;
}