body {
  direction: rtl !important;
  text-align: right;
  position: relative;
  font-family: Tahoma, monospace, Geneva, Verdana, sans-serif;
  font-size: 14px;
}

img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-select: none;
}

a {
  text-decoration: none !important;
  font-weight: bold;
  color: #71D000;
}

a:hover {
  color: #00BC00;
}

a:active {
  color: #FF8000;
}

.middle-row {
  margin-top: 20px !important;
  min-height: 550px;
  padding-bottom: 5vh !important;
}

.navi-col {
  background: transparent url(./images/shadow_a.png) no-repeat left top;
}

.side-col {
  background: transparent url(./images/shadow_b.png) no-repeat right top;
}

.middle-col {
  text-align: center;
}

.footer-row {
  background-color: #e7e7e7;
  width: 100%;
  padding: 5px;
  text-align: center;
  border-top: 1px solid silver;
}

.side_navi, .side_info, .content {
  margin-top: 20px;
}

#root>div {
  width: 100%;
  padding: 0;
}

#root>div>.row {
  margin: 0;
}

#root>div>.row>.col {
  padding: 0;
}